import React, {useEffect, useRef, useState} from 'react';
import {useParams} from 'react-router-dom'
import Chatmain from "./chatmain";
import {Spin} from "antd";

const Message = () => {

    const message = useRef([]);
    const {id} = useParams();
    const [user,setUser] = useState({});
    const [load,setLoad] = useState(false);

    const userDetail = async () => {
        const res = await fetch(`http://121.196.180.250:3000/user/detail?uid=${id}`);
        const data = await res.json();
        setUser(data);
    }

    const historyMessage = async () => {
        setLoad(false)
        message.current = [];
        const res = await fetch(`http://121.196.180.250:3000/msg/private/history?uid=${id}&cookie=${localStorage.neteaseCookie}`,{
            credentials:"include",
            mode:"cors"
        });
        const data = await res.json();
        const length = data.msgs.length-1;
        data.msgs.map((item,index) => {
            const textmsg = JSON.parse(item.msg);
            if(item.fromUser.userId == id){
                if(textmsg.type != 16){
                    message.current[length - index] =  {type:"text",content:{text:textmsg.msg},position:"left",createdAt:item.time,hasTime:true};
                }else if(textmsg.type == 16){
                    message.current[length - index] = {type: "image",content: {picUrl:textmsg.picInfo.picUrl},position:"left",createdAt:item.time,hasTime:true}
                }
            }else{
                if(textmsg.type != 16){
                    message.current[length - index] =  {type:"text",content:{text:textmsg.msg},position:"right",createdAt:item.time,hasTime:true};
                }else if(textmsg.type == 16){
                    message.current[length - index] = {type: "image",content: {picUrl:textmsg.picInfo.picUrl},position:"right",createdAt:item.time,hasTime:true}
                }
            }
        })
        setLoad(true);
    }

    useEffect(()=>{
        userDetail();
        historyMessage();
    },[id])


    return (
        <div style={{height:"90vh"}}>
            <div style={{height:"90vh",width:"56vw"}}>
                {user.profile && load &&
                <Chatmain id={id} user={user} message={message.current}/>}
            </div>
        </div>
    );
};

export default Message;
